*,
*::before,
*::after {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

:root {
  /* Colors */
  --base-color-300: #FFA370;
  --base-color-500: #FF470B;

  /* Neutral colors */
  --neutral-white: #FFFFFF;
  --neutral-200: #EAECF0;
  --neutral-400: #98A2B3;

  /* CTA colors */
  --cta-base: #686D72;

  /* Text-styles */
  --font-color-head: #101828;
  --font-color-body: #344054;
  --font-color-placeholder: #98A2B3;

  /* Font sizes */
  --size-heading1: 3rem;

  /* Line-height */
  --line-height-heading: -1.2;
}

h1 {
  font-size: 4rem;
}

h1, h2, h3, h4, h5, h6 {
  font-family: 'Roboto', sans-serif;
  line-height: var(--line-height-heading);
}

.container {
  height: 100vh;
}

.footer {
  background-color: var(--cta-base);
  color: var(--neutral-white);
  margin-top: auto; 
}

.footer__container {
  display: flex;
  flex-direction: column;
  padding: 20px;
}

.footer__left {
  margin-bottom: 20px;
}

.footer__logo {
  width: 50%;
  margin-bottom: 10px;
}

.footer__text {
  font-size: 16px;
  margin-bottom: 8px;
}

.footer__email,
.footer__phone {
  text-decoration: underline 1px;
  font-size: 14px;
}

.footer__email a,
.footer__phone a {
  text-decoration: none;
}

.footer__right {
  display: flex;
  flex-direction: column;
}

.footer__column {
  margin-bottom: 20px;
}

.footer__column-heading {
  font-size: 18px;
  margin-bottom: 8px;
}

.footer__links {
  list-style-type: none;
}

.footer__links li {
  margin-bottom: 8px;
}

.footer__links a {
  text-decoration: none;
}

@media (min-width: 1024px) {
  .footer__container {
    flex-direction: row;
    justify-content: space-between;
    align-items: flex-start;
    max-width: 1440px;
    margin: 0 auto;
    padding: 40px;
  }

  .footer__left {
    text-align: left;
    width: 30%;
  }

  .footer__right {
    display: flex;
    justify-content: space-between;
    width: 65%;
  }

  .footer__column {
    width: 30%;
  }
}

.header {
  position: sticky;
  top: 0;
  padding: 10px 20px;
  z-index: 1000;

  &__container {
    display: flex;
    justify-content: space-between;
    align-items: center;
    width: 100%;
    max-width: 1440px; 
    margin: 0 auto;
    padding: 0 160px; 
  }

  &__logo img {
    width: 190px;
  }

  &__mobile-toggle {
    display: none;
    background: none;
    border: none;
    cursor: pointer;

    &.active + .header__nav {
      display: block;
    }
  }

  &__nav {
    display: flex;
    justify-content: flex-end;

    &-list {
      list-style: none;
      display: flex;
      align-items: center; /* Ensures horizontal alignment */
    }

    &-item {
      margin-left: 20px;

      &--dropdown {
        position: relative;

        & button{
          background-color: transparent;
          border: none;
        }
        &:hover .header__dropdown-menu {
          display: block;
        }

        .header__dropdown-menu {
          display: none;
          position: absolute;
          top: 100%;
          left: 0;
          background: var(--neutral-white);
          z-index: 100;
          list-style: none;

          & li a{
            color: var(--font-color-body);
            text-decoration: none;
          }
        }
      }

      &--search {
        .header__search {
          display: flex;
          align-items: center;
          position: relative;
          background: var(--neutral-white);
          border: 1px solid var(--neutral-200);
          padding: 9px;
          border-radius: 5px;

          &-input {
            padding: 5px 10px;
            padding-left: 30px; 
            border: none;
            outline: none;
            color: var(--neutral-400);
            width: 200px;
          }

          &-btn {
            position: absolute;
            left: 10px; 
            background: none;
            border: none;
            cursor: pointer;
            padding: 5px;
          }
        }
      }
    }

    &-link {
      color: var(--font-color-body);
      text-decoration: none;
      font-size: 16px;

      &--login {
        font-weight: bold;
      }
    }
  }

  &__contact-btn {
    background: var(--base-color-500);
    color: var(--neutral-white);
    padding: 10px 20px;
    border-radius: 30px;
    text-decoration: none;
  }
}

@media (max-width: 900px) {
  .header {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    padding: 10px 12px; 
  }

  .header__container {
    padding: 0 12px;
  }

  .header__nav {
    display: none;
    width: 100%;
    flex-direction: column;
    background-color: var(--neutral-white);
    position: absolute;
    top: 60px; 
    left: 0;
    padding: 20px 0;
    z-index: 999;
  }

  .header__mobile-toggle.active + .header__nav {
    display: flex;
  }

  .header__mobile-toggle {
    display: block;
    background: none;
    border: none;
    cursor: pointer;
    font-size: 30px;
    margin-left: auto; 
    color: var(--base-color-500); 
  }

  .header__nav-list {
    display: flex;
    flex-direction: column;
    align-items: center;
  }

  .header__nav-item {
    margin: 10px 0;
  }

  .header__nav-item--dropdown .header__dropdown-menu {
    display: none;
  }

  .header__nav-item--dropdown:hover .header__dropdown-menu {
    display: block;
  }
}

@media (max-width: 480px) {

  .header__logo img {
    width: 150px; 
  }

  .header__mobile-icon {
    font-size: 25px;
  }
}

.blog-container {
  margin: 0 auto;
  max-width: 1200px;
  padding: 0 160px;
  height: 100vh;
}

.blog-grid {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(356px, 1fr));
  height: 100%;
}

/* Blog Card */
.blog-card {
  background-color: green;
  border: 1px solid #e0e0e0;
  border-radius: 10px;
  overflow: hidden;
  transition: transform 0.2s, box-shadow 0.2s;
}

.blog-card:hover {
  transform: translateY(-5px);
  box-shadow: 0 4px 10px rgba(0, 0, 0, 0.1);
}

.blog-card__header {
  position: relative;
  overflow: hidden;
}

.blog-card__thumbnail img {
  width: 100%;
  height: auto;
  object-fit: cover;
  aspect-ratio: 1 / 1; /* Makes the image a square */
}

.blog-card__details {
  padding: 15px;
  border-top: 1px solid #e0e0e0;
}

.blog-card__title {
  font-size: 18px;
  font-weight: bold;
  margin: 10px 0;
}

.blog-card__title a {
  text-decoration: none;
  color: var(--font-color-body)
}

.blog-card__meta {
  display: flex;
  margin-left: 10px;
  gap: 10px;
  font-size: 14px;
  color: var(--font-color-body);
}

.blog-card__meta-category {
  background-color: var(--base-color-300);
  border: solid 1px var(--base-color-500);
  width: 100px;
  height: 28px;
  border-radius: 30px;
  padding: 2px 16px;

  & a{
    text-decoration: none;
    color: var(--font-color-body);
  }
}

.blog-card__meta-location,
.blog-card__meta-time {
  display: block;
  align-items: center;
}

.blog-card__meta-location i,
.blog-card__meta-time i {
  padding-left: 15px;
  color: var(--font-color-body)
}
